.superhero-container {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 200px;
  height: auto;

  background-color: #ffffff;
  /* border: 1px solid grey; */
  border-radius: 25px;
  padding: 25px;
  cursor: pointer;
  -moz-osx-font-smoothing: grayscale;
  backface-visibility: hidden;
  transform: translateZ(0);
  transition: transform 0.25s ease-out;
  align-self: center;
  overflow: visible;
}
.superhero-container h1 {
  align-self: center;
  text-align: center;
  text-transform: uppercase;
}
img {
  position: relative;
  width: 100%;
  align-self: center;
  object-fit: cover;
  object-position: 50% 50%;
  border-radius: 25px;
}
.superhero-container:hover {
  transform: scale(1.1);
  align-self: center;
  box-shadow: 10px 10px 5px grey;
}
